body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.page {
  left:0;
  right: 0;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  align-items: center;
  justify-content: center;
}

.frontpage {
  height: 100vh;
  background: 
      /* top, transparent black, faked with gradient */ 
      linear-gradient(
      rgba(0, 0, 0, 0.5), 
      rgba(0, 0, 0, 0.7)
      ),
      /* bottom, image */
      url('http://blog.eranurag.com/wp-content/uploads/2017/03/header.jpg');
}

.frontdiv{
  text-align: center;    
}

.mainpic {
  width: 18vw;
  height: 18vw;
  border: 10px solid white;
  border-radius: 1000px;
}

.mainname {
  font-size: 2.5em;
  color: white;
}

.maindesc {
  font-size: 1.5em;
  color: white;
}

.mainicon {
  font-size: 4em;
  color: white;
  margin: 10px;
  transition: 0.3s;
}

.mainicon.fa-github:hover {
  color: #6cc644;
}

.mainicon.fa-linkedin:hover {
  color: #0077B5;
}

.niceheader {
  margin: 1em;
  font-size: 4em;
}

.skillspage {
  flex-direction: column
}

.skillcard {
  margin: 2em;
  width: 10%;
  flex-direction: column;
}

.skillimage {
  width: 50%;
}

.skilltitle {
  font-size: 1.5em;
}

.rowholder {
  flex-direction: column;
}

.subheading {
  font-size: 2em;
}

.contactpage {
  color: white;
  background: #5433FF;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #A5FECB, #20BDFF, #5433FF);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #A5FECB, #20BDFF, #5433FF); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.mailform {
  width: 40vw;
  margin: 2em;
}